var slice = require('./slice')

/**
  * ,
  *
  * @param {Function} callback 
  * @param {Object} context 
  * @param {*} args 
  * @return {Object}
  */
function once (callback, context) {
  var done = false
  var rest = null
  var args = slice(arguments, 2)
  return function () {
    if (done) {
      return rest
    }
    rest = callback.apply(context, slice(arguments).concat(args))
    done = true
    return rest
  }
}

module.exports = once
