var toValueString = require('./toValueString')

var helperStringRepeat = require('./helperStringRepeat')

/**
  *  n 
  *
  * @param {String} str 
  * @param {Number} count 
  * @return {String}
  */
function repeat (str, count) {
  return helperStringRepeat(toValueString(str), count)
}

module.exports = repeat
