var slice = require('./slice')

/**
  *  setTimeout 
  *
  * @param {Function} callback 
  * @param {Number} wait 
  * @param {*} args 
  * @return {Number}
 */
function delay (callback, wait) {
  var args = slice(arguments, 2)
  var context = this
  return setTimeout(function () {
    callback.apply(context, args)
  }, wait)
}

module.exports = delay
