var slice = require('./slice')

/**
  * 
  *
  * @param {Function} callback 
  * @param {Object} context 
  * @param {*} args 
  * @return {Object}
  */
function bind (callback, context) {
  var args = slice(arguments, 2)
  return function () {
    return callback.apply(context, slice(arguments).concat(args))
  }
}

module.exports = bind
