var hasOwnProp = require('./hasOwnProp')

/**
  * ,truefalse
  *
  * @param {Object} obj 
  * @param {Object} val 
  * @return {Boolean}
  */
function includes (obj, val) {
  if (obj) {
    if (obj.includes) {
      return obj.includes(val)
    }
    for (var key in obj) {
      if (hasOwnProp(obj, key)) {
        if (val === obj[key]) {
          return true
        }
      }
    }
  }
  return false
}

module.exports = includes
