var uniq = require('./uniq')
var toArray = require('./toArray')

/**
  * 
  *
  * @param {...Array} 
  * @return {Array}
  */
function union () {
  var args = arguments
  var result = []
  var index = 0
  var len = args.length
  for (; index < len; index++) {
    result = result.concat(toArray(args[index]))
  }
  return uniq(result)
}

module.exports = union
